body.dark {
  background-color: $dark-bg-color;
}

.theme {
  gap: 8px;

  @media screen and (max-width: 767px) {
    display: none;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;

    &.mobile {
      display: flex;
      position: absolute;
      z-index: 6;
      bottom: 18;
      opacity: 100%;
      pointer-events: all;
      visibility: inherit;
    }
  }

  @include tablet {
    display: flex;
    width: 98px;
    height: 21px;
    margin-left: 24px;
    margin-top: 49px;
    margin-bottom: 36px;
  }

  @include desktop {
    gap: 12px;
    width: 148px;
    height: 24px;
    margin: 0;
    margin-top: 54px;
    margin-bottom: 35px;
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $light-bg-color;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border: 1px solid $accent-color;
}

.slider:before {
  position: absolute;
  content: '';
  height: 16px;
  width: 16px;
  left: 1px;
  bottom: 1px;
  background-color: $accent-color;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider:before {
  background-color: $light-bg-color;
}

input:checked + .slider {
  background-color: $accent-color;
  border: 1px solid $light-bg-color;
}

input:focus + .slider {
  box-shadow: 0 0 1px $accent-color;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.theme__dark {
  display: none;
}
.theme__light {
  display: none;
}
@include desktop {
  .theme {
    gap: 12px;
  }

  .theme__svg {
    display: none;
  }
  .theme__dark {
    display: block;
    margin: 0;
    font-size: 18px;
    line-height: 1.3;

    color: $secondary-text-color;
  }
  .theme__light {
    display: block;
    font-size: 18px;
    line-height: 1.3;
  }
  .opacityForDark {
    opacity: 0.4;
  }
}

.theme__light {
  color: $accent-color;
}

.sun {
  fill: $accent-color;
}
