.slider {
  cursor: pointer;
  background-color: #f4f4f4;
  border: 1px solid #4440f6;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4440f6;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider:before {
  background-color: #f4f4f4;
}

